<!--搜索地址页面，点击进入外卖首页-->


<template>
    <div class="root">
        <header>
            <router-link to="/Home"><i class="el-icon-arrow-left"></i></router-link>
            <span>{{cityName}}</span>
            <router-link to="/Home">切换城市</router-link>
        </header>
        <form action="" class="city_form">
            <div>
                <el-input type="text" placeholder="输入学校、商务楼、地址" class="city_input" v-model="inputValue"  clearable></el-input>
            </div>
            <div><input type="submit" class="city_submit" @click="searchV"></div>
        </form>
        <!--历史记录-->
        <h5 class="pois_search_history" v-if="isShow1">搜索历史</h5>
        <ul class="getpois_ul" v-if="isShow">
            <li v-for="address in localAddress" @click="goShop(address)">
                <h4 class="pois_name ellipsis">{{address.name}}</h4>
                <p class="pois_address ellipsis">{{address.address}}</p>
            </li>
        </ul>
        <footer class="clear_all_history" v-if="isShow" @click="clearAllHistory">清空所有</footer>
        <!--搜索记录-->
        <ul class="getpois_ul">
            <li v-for="allAddress in searchValue" @click="memoryAddress(allAddress)">
                <h4 class="pois_name ellipsis">{{allAddress.name}}</h4>
                <p class="pois_address ellipsis">{{allAddress.address}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "City",
        data(){
            return{
                //城市ID
                cityId:0,
                //城市名字
                cityName:"",
                //输入框搜索关键字
                inputValue:"",
                //搜索到的地址
                searchValue:[],
                //localstorage里的地址
                localAddress:[],
                //搜索历史四个字的显隐
                isShow :true,
                //搜索历史记录标签的显隐
                isShow1:true,
                //输入框小图标的显隐
                isShow2:false,
            }
        },
        created(){
            this.cityId = this.$route.params.cityId
            this.axios.get("v1/cities/"+this.cityId).then(res=>{
                // console.log(res);
                this.cityName = res.name
            }).catch(err=>{

            })
            if (localStorage.getItem('placeHistory')){
                this.localAddress = JSON.parse(localStorage.getItem('placeHistory'));
            }else {
                this.isShow = false;
            }

        },
        methods:{
            //获取搜索关键字的地址
            searchV(){

                if (this.inputValue){
                    this.isShow = false;
                    this.isShow1 = false;
                    this.axios.get("v1/pois",{
                        params:{
                            city_id:this.cityId,
                            keyword:this.inputValue
                        }
                    }).then(res=>{
                        console.log(res)
                        this.searchValue = res;
                    }).catch(err=>{
                    })
                } else {
                    alert("请输入地址");
                }



            },
            //存入放历史记录localstorage
            memoryAddress(address){
                console.log(address);
                let obj = {}
                obj = address;
                console.log(obj);
                let a;
                if (localStorage.getItem('placeHistory')== null){
                    a=[];
                    a.push(address)
                    localStorage.setItem('placeHistory',JSON.stringify(a));
                } else {
                    a = JSON.parse(localStorage.getItem('placeHistory'));
                    //去重逻辑
                    a.forEach(item=>{
                        if (item.name == address.name){
                            console.log("111")
                        }else {
                            a.push(address)
                            localStorage.setItem('placeHistory',JSON.stringify(a));
                        }
                    });
                };
                this.$router.push({
                    path:'/msite',
                    query:{
                        geohash:address.geohash,
                        latitude:address.latitude,
                        longitude:address.longitude,
                    }
                })

            },
            clearAllHistory(){
                localStorage.removeItem('placeHistory');
                this.isShow =false
            },
            goShop(address){
                this.$router.push({
                    path:'/msite',
                    query:{
                        geohash:address.geohash,
                        latitude:address.latitude,
                        longitude:address.longitude,
                    }
                })
            }

        },

    }

</script>

<style scoped>
    .root {
        width: 100%;
        height: 1.95rem;
        background-color: #f5f5f5;
    }
    header{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
    }
    header>a:nth-of-type(1){
        left: .4rem;
    }
    header>a:nth-of-type(2){
        right: .4rem;
        font-size: .6rem;
        text-decoration: none;
    }
    header>span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight: 700;
    }
    .city_form {
        margin-top: 2.35rem;
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-top: .5rem;
    }
    .city_form div{
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .city_form >div>.city_input {
        width: 100%;
        margin-bottom: .4rem;
    }
    .city_form div .city_submit {
        background-color: #3190e8;
        font-size: .65rem;
        color: #fff;
        border-radius: .25rem;
        margin-bottom: .4rem;
        width: 100%;
        height: 1.6rem;
        outline: none;
        -webkit-appearance: none;
        /*margin-top: .4rem;*/
    }
    .getpois_ul {
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
    }
    .getpois_ul li {
        margin: 0 auto;
        padding-top: .75rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .getpois_ul li .pois_name {
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .getpois_ul li .pois_address {
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .pois_search_history {
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    .clear_all_history {
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
    .el-input--suffix .el-input__inner {
        height: 1.5rem;
    }

</style>